/* header */
.social-baner-header {
   height: 5vmin;
   width: 100%;
   background-color:white;
   display: flex;
   align-items:center;
   justify-content:space-between;
   flex-direction: row-reverse;
}
.link-sociales{
   width: 12%;
   height: 100%;
   padding-right: 1.5%;
   display: inline-flex;
   justify-content: space-between;
   align-items: center;
   flex-direction: row-reverse;
}
.socialStyles{
  width: 1.7rem; cursor:pointer;
}
.baner{
  width: 90%; overflow: hidden;
}
.baner h6{
   font-size: 1.2em;
   color: #6335ccff;
   text-align: end;
   animation: baner 20s linear infinite;
}
@keyframes baner {
   0%{transform: translateX(100%);}
   100%{transform: translateX(-100%);}
}
.App-logo{
   display: inline-flex;
   align-items: center;
   height: 100%;
}
.App-logo a{
   display: inline-flex;
   justify-content: center;
   align-items: center;
}
.App-logo img{width: 15vmax;}
nav{background-color:white !important; display:flex; justify-content:center;}
nav ul{width: 85%; height: 100%;}
nav ul a{color:#6335ccff; }
nav ul li{width:20%; text-align: center; height: 100%; font-family:'MonteserratMedium';}
.nav-wrapper{width: 99%;}
/* Menu Dropdown */
#dropdownMenu{
  width: 28% !important;
  background-color:white;
  z-index: 10;
  position: fixed;
  margin:0%;
  border-left: solid 0.5px rgb(128 128 128 / 20%);
  border-right: solid 0.5px rgb(128 128 128 / 20%);
  border-bottom: solid 0.5px rgb(128 128 128 / 20%);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  box-shadow: 0px 1px 1px;
}
#dropdownMenu .circle{
   background-color: white !important;
   border: 2px outset #aaaaaa20 !important;
}
.listmenu{
  transition: background-color .5s;
}
.listmenu:hover{
  cursor:pointer;
  background-color: rgb(128 128 128 / 8%);
  border-radius: 5px;
}

.bar{

   display: none;

}

ul li a{

   cursor: url(../assets/images/cursorpointer.png), pointer;
 }
 

@media screen and (max-width: 960px) {
   .bar{
   display: flex;
   align-items: center;
   cursor:pointer; 
   color: black;  
   font-size: 2em;
   margin-right: 0.5rem;
   
  
}


 




}

